<template lang="pug">
    .tests__content
        .tests__header
            h4.tests__title Список доступных тестов
            .tests__filter
                label.label__filter(for='subjects') Выберите предмет:
                ul.subjects__list
                    select(v-model="selectedSubjectId").tests__filter-select
                        option(value=0) Все
                        option(:value='item.id' v-for='item in subjects') {{item.name}}
        ul.tests__list 
            .tests__item.names
                .test__item-row
                    .test__item-row(v-if='idx==0').names
                    .tests__number #
                    .tests__code Код теста
                    .tests__subject Предмет
                    .tests__class Класс
                    .tests__class Время (мин)
                    .tests__pass Выбрать
            li.tests__item(v-for="test,idx in filteredTests" :key="test.id")
                available-test-item(
                    :idx="idx"
                    :test="test"
                    :userId='user.id'
                    @passTestAvailable="passTestAvailable"
                    )

</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        availableTestItem: () => import("../../scholar/tests/available-test-item")
    },
    created() {
        this.fetchAvailableScholarTests(this.user.id),
        this.fetchSubjects()
    },
    beforeRouteLeave (to, from, next) {
        if(!this.available)
            next(false)
        else next()
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    computed: {
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("scholarAvailableTests", {
            scholarAvailableTests: state => state.scholarAvailableTests}),
        ...mapState ("subjects", {
            subjects: state => state.subjects}),
        filteredTests: function() {
            return this.scholarAvailableTests
            //filter by subject
            .filter(test => {
                return this.selectedSubjectId == 0 || test.group.subject.id== this.selectedSubjectId;
            })
        }
    },
    methods: {
        ...mapActions("scholarAvailableTests", ["fetchAvailableScholarTests"]),
        ...mapActions("subjects", ["fetchSubjectsForScholar"]),
        passTestAvailable(data) {
            this.available = data.available
        }
    },
    data () {
        return {
            available: true,
            selectedSubjectId: 0,
            idx: 0,
        }
    }
}
</script>
